const { createSlice } = require("@reduxjs/toolkit");

interface stateIt {
    userId: string,
    token: string,
    email: string,
    connected: boolean, 
    isAdmin: number
}
export const sessionSlice: any = createSlice({
    name: 'session',
    initialState: {userId: "",token: "",email: "", connected: false, isAdmin: 0},

    reducers: {
        connectUser: (state: stateIt, action: any) => {
            // {type: "session/connectUser" payload: 1}
            Object.assign(state, action.payload)
            
        },
    }
})

export const {connectUser} = sessionSlice.actions