import React from 'react'
import SideMenu from '../Components/SideMenu'

const Contact = () => {
  return (
    <div id='contact_page' className='layout'>
      <nav>
        <SideMenu />
      </nav>
      <section>
        <main>
          <div
            className="illustration animated animatedFadeInUp fadeInUp"
          >
            <img src={`${process.env.PUBLIC_URL}images/undraw_profile_details_re_ch9r.svg`} alt="about" />
          </div>
        </main>
        <aside>
          <div
            className="animated animatedFadeInUp fadeInUp"
            style={{ animationDelay: "200ms" }}
          >
            <h1 className="app_title">
              Contact <strong>Us</strong> for more information
            </h1>
            <span style={{ paddingLeft: 10, color: "gray" }}>réseau sociaux</span>
            <div className="social_icons">
              <a href="/" target='_blank'><i className='fa fa-facebook' style={{ backgroundColor: '#1977f3' }}></i></a>
              <a href="/" target='_blank'><i className='fa fa-youtube' style={{ backgroundColor: '#dd1010' }}></i></a>
              <a href="/" target='_blank'><i className='fa fa-github' style={{ backgroundColor: '#071d2b' }}></i></a>
              <a href="/" target='_blank'><i className='fa fa-whatsapp' style={{ backgroundColor: '#03c100' }}></i></a>
            </div>
          </div>

          <div
            className="contact_form animated animatedFadeInUp fadeInUp"
            style={{ animationDelay: "300ms" }}
          >
            <form>
              <input type="text" placeholder='Votre Nom' />
              <input type="email" placeholder='Email' />
              <textarea placeholder='message'></textarea>
              <button type="submit">Se connecter</button>
            </form>

          </div>
        </aside>
      </section>
    </div>
  )
}

export default Contact
