import React from 'react'
import SideMenu from '../Components/SideMenu'

const Settings = () => {
  return (
    <div id='settings_page' className='layout'>
      <nav>
        <SideMenu />
      </nav>
      <main>
        <img
          className='animated animatedFadeInUp fadeInUp'
          style={{ maxWidth: 300, width: "100%" }}
          src={`${process.env.PUBLIC_URL}images/undraw_active_options_re_8rj3.svg`}
          alt="settings"
        />
      </main>
    </div>
  )
}

export default Settings
