
const MessageBox = (props: any) => {
  const { type, message, show } = props.showMessage;

  setTimeout(() => {
    props.setShowMessage({ show: false });
  }, 1000000)
  return (
    <>
      {show &&
        <p
          id='message_box'
          className={type}
        >
          <i className='fa fa-exclamation-triangle'></i> <em>{message}</em>
        </p>
      }
    </>
  )
}

export default MessageBox