import React, { useState } from 'react'
import InputPassword from './InputPassword';
import axios from "axios"
import { useDispatch, useSelector } from 'react-redux';
import { connectUser } from '../feature/session.slice';



const SignInForm = (props: any) => {
    const { setShowMessage } = props;

    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")

    const userSession = useSelector((state: any) => state.session)
    const dispatch: any = useDispatch();

    const userData: { email: string, password: string } = {
        email: email,
        password: password,
    }

    const handlePassword: any = (e: any) => {
        setPassword(e.target.value);
    }

    const isEmail: any = () => {
        let element: any = document.getElementById("email") as HTMLInputElement
        let error: any = document.getElementById("email_error") as HTMLInputElement
        let regex: any = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/

        if (!regex.test(email)) {
            element.style.borderColor = "var(--red)"
            error.style.display = "block"
            return false
        } else {
            error.style.display = "none"
            element.style.borderColor = "var(--green)"
        }
        return true
    }
    const handleSignIn: any = (e: any) => {
        e.preventDefault()
        if (isEmail()) {
            axios.post(`/users/sign-in`, userData)
                .then(res => {
                    const { data } = res.data;
                    // data.connected = false;
                    data.isAdmin = 10;
                    console.log(data);
                    dispatch(connectUser(data))

                    console.log("Usersession", userSession);

                    // setEmail("")
                    // setPassword("")
                    // window.location.assign("/home")
                })
                .catch(error => {
                    setShowMessage({
                        type: "warning",
                        message: error.response.data.message,
                        show: true
                    })
                })
        }
    }
    return (
        <div className="login_form">
            <form
                className="animated animatedFadeInUp fadeInUp"
            >
                <input
                    type="email"
                    placeholder='email'
                    value={email}
                    onChange={(e: any) => setEmail(e.target.value)}
                    id="email"
                />
                <span id='email_error'>l'Email n'est pas valide !</span>
                <InputPassword
                    password={password}
                    handlePassword={handlePassword}
                />
                <span id='password_error'>8 charactère minimum pour le mot de passe</span>

                <button
                    type="submit"
                    onClick={handleSignIn}
                    style={email === "" || password === "" ? { backgroundColor: "#cdcdcd", pointerEvents: "none", marginTop: 10 } : { marginTop: 10 }}
                >Se connecter</button>
            </form>
        </div>
    )
}

export default SignInForm
