import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'

const SideMenu = () => {
    const screenSize: any = window.matchMedia('(max-width: 950px)').matches
    const [toggle, setToggle] = useState(screenSize);

    return (
        <>
            <nav id="side_menu" className={toggle ? "close" : ''}>
                <div className="mobile_top_bar" onClick={() => setToggle(!toggle)}>
                    <i className={toggle ? "fa fa-bars" : "fa fa-close"}></i>
                </div>
                <nav className='nav_list' role="navigation">
                    <ul>
                        <li>
                            <NavLink
                                to="/"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            >Home</NavLink>
                        </li>
                        <li>
                            <NavLink
                                to="/contact"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            >Contact</NavLink>
                        </li>
                        <li>
                            <NavLink
                                to="/settings"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            >Paramètre</NavLink>
                        </li>
                        <li>
                            <NavLink
                                to="/about"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            >About</NavLink>
                        </li>
                        <li>
                            <NavLink
                                to="/sign"
                                className={({ isActive }) => (isActive ? 'active' : '')}
                            >Se connecter</NavLink>
                        </li>
                    </ul>
                </nav>
            </nav>
        </>
    )
}

export default SideMenu
