import React, { useState } from 'react'
import axios from "axios"
import InputPassword from './InputPassword'



const SignUpForm = (props: any) => {
    const { setForm, setShowMessage } = props
    const [fullname, setFullname] = useState("")
    const [email, setEmail] = useState("")
    const [speciality, setSpeciality] = useState("")
    const [password, setPassword] = useState("")

    interface user {
        fullName: string,
        email: string,
        speciality: string,
        password: string,
    }

    const userData: user = {
        fullName: fullname,
        email: email,
        speciality: speciality,
        password: password,
    }

    const selectData: Array<any> = [
        { short_libele: "GL", libele: "génie logiciel" },
        { short_libele: "GSI", libele: "gestion des système informatique" },
        { short_libele: "MCV", libele: "marqueting commerce vente" },
        { short_libele: "BF", libele: "banque et finance" },
        { short_libele: "MC", libele: "mecatronique" },
        { short_libele: "RSI", libele: "réseau et sécurité informatique" },
        { short_libele: "IH", libele: "industrie d'habiellement" },
        { short_libele: "HR", libele: "hotélerie et restauration" },
        { short_libele: "RT", libele: "réseau et télécome" }
    ]

    const handleFullname: any = (e: any) => {
        setFullname(e.target.value);
    }
    const handleEmail: any = (e: any) => {
        setEmail(e.target.value);
    }
    const handleSpeciality: any = (e: any) => {
        setSpeciality(e.target.value);
    }
    const handlePassword: any = (e: any) => {
        setPassword(e.target.value);
    }

    const handleSignUp: any = (e: any) => {
        e.preventDefault()
        if (isFullName() & isEmail() & isPassword() & isSpeciality()) {
            axios.post(`/users/sign-up`, userData)
                .then(res => {
                    console.log(res.data);
                    setForm("sign-in")
                    // setLogEmail(email)
                    setEmail("")
                    setFullname("")
                    setPassword("")

                    setShowMessage({
                        type: "success",
                        message: res.data.message,
                        show: true
                    })

                })
                .catch(error => {
                    setShowMessage({
                        type: "warning",
                        message: error.response.data.message,
                        show: true
                    })
                })
        }
    }

    const isFullName: any = () => {
        let element: any = document.getElementById("fullname") as HTMLInputElement
        let error: any = document.getElementById("fullname_error") as HTMLInputElement
        let regex: any = /(?=^.{0,40}$)^[a-zA-Z-]+[a-zA-Z-]+$/

        if (fullname.match(regex)) {
            element.style.borderColor = "var(--red)"
            error.style.display = "block"
            return false
        } else {
            element.style.borderColor = "var(--green)"
            error.style.display = "none"
        }
        return true
    }
    const isEmail: any = () => {
        let element: any = document.getElementById("email") as HTMLInputElement
        let error: any = document.getElementById("email_error") as HTMLInputElement
        let regex: any = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/

        if (!regex.test(email)) {
            element.style.borderColor = "var(--red)"
            error.style.display = "block"
            return false
        } else {
            error.style.display = "none"
            element.style.borderColor = "var(--green)"
        }
        return true
    }
    const isSpeciality: any = () => {
        let element: any = document.getElementById("speciality") as HTMLInputElement
        let error: any = document.getElementById("speciality_error") as HTMLInputElement
        let valid: boolean = false
        selectData.forEach((el: any) => {
            if (speciality === el.libele) {
                element.style.borderColor = "var(--green)"
                error.style.display = "none"
                valid = true
                return valid
            }
        })
        if (!valid) {
            element.style.borderColor = "var(--red)"
            error.style.display = "block"
        }
        return valid
    }
    const isPassword: any = () => {
        let element: any = document.getElementById("password") as HTMLInputElement
        let error: any = document.getElementById("password_error") as HTMLInputElement
        if (password.length < 8) {
            element.style.borderColor = "var(--red)"
            error.style.display = "block"
            return false
        } else {
            element.style.borderColor = "var(--green)"
            error.style.display = "none"

        }
        return true
    }

    return (
        <div className="login_form">
            <form className="animated animatedFadeInUp fadeInUp">
                <label htmlFor="">Nom Complet</label>
                <input
                    type="text"
                    placeholder='Entrer votre Nom Complet'
                    onChange={handleFullname}
                    value={fullname}
                    id="fullname"
                />
                <span id='fullname_error'>Saisissez votre véritable nom svp !</span>
                <label htmlFor="">Email</label>
                <input
                    type="email"
                    placeholder='example@gmail.com'
                    onChange={handleEmail}
                    value={email}
                    id="email"
                />
                <span id='email_error'>l'Email n'est pas valide !</span>
                <label htmlFor="">Spécialité</label>
                <input
                    list="data"
                    onChange={handleSpeciality}
                    value={speciality}
                    placeholder="Selectionner votre spécialité"
                    id='speciality'
                />
                <datalist id="data" style={{ width: 500 }}>
                    {selectData.map((option: { libele: string, short_libele: string }, key: number) => <option key={key}>{option.libele}</option>)}
                </datalist>
                <span id='speciality_error'>Sellectionnez une spécialité dans la liste !</span>
                <label htmlFor="">Mot De Passe</label>
                <InputPassword
                    password={password}
                    handlePassword={handlePassword}
                />
                <span id='password_error'>8 charactère minimum pour le mot de passe</span>
                <button
                    type="submit"
                    onClick={handleSignUp}
                    style={fullname === "" || email === "" || speciality === "" || password === "" ? { backgroundColor: "#cdcdcd", pointerEvents: "none", marginTop: 10 } : { marginTop: 10 }}
                >S'enregistre</button>
            </form>
        </div>
    )
}

export default SignUpForm
