import React, { useState } from 'react'
import AsideBar from '../Components/AsideBar'
import SideMenu from '../Components/SideMenu'
import MessageBox from '../Components/MessageBox'
import SignInForm from '../Components/SignInForm'
import SignUpForm from '../Components/SignUpForm'
import { useSelector } from 'react-redux'

const LogIn = () => {
  const [form, setForm] = useState("sign-in")
  const [showMessage, setShowMessage] = useState({ type: "", message: "", show: false })

  const session: any = useSelector((store: any) => store.session)


  console.log(session)


  return (
    <div id='LogIn_page' className='layout'>
      <nav>
        <SideMenu />
      </nav>
      <main>
        <nav className='nav_login'>
          <ul>
            <li>
              <span
                onClick={() => setForm("sign-in")}
                className={form === "sign-in" ? 'active' : ''}
              >Se Connecter</span>
            </li>
            <li>
              <span
                onClick={() => setForm("sign-up")}
                className={form === "sign-up" ? 'active' : ''}
              >Créer un compte</span>
            </li>

          </ul>
        </nav>
        <MessageBox
          showMessage={showMessage}
          setShowMessage={setShowMessage}
        />
        {
          form === "sign-in" &&
          <SignInForm
            setForm={setForm}
            setShowMessage={setShowMessage}
          />}
        {
          form === "sign-up" &&
          <SignUpForm
            setForm={setForm}
            setShowMessage={setShowMessage}
          />
        }
      </main>
      <aside>
        <AsideBar />
      </aside>
    </div>
  )
}


export default LogIn

