import React from 'react'
import { NavLink } from 'react-router-dom';



const Card = (props: any) => {
    const { image, theme, option, niveau, date, description, id } = props.book;

    return (
        <div
            className="card animated animatedFadeInUp fadeInUp"
            style={{ animationDelay: `${2 + props.count}00ms` }}
        >
            <NavLink
                to={`/datail/${id}`}
                className="image"
                style={{
                    backgroundImage: `url("${process.env.PUBLIC_URL}images/${image}")`
                }}
            > </NavLink>
            <div className="text-content">
                <h3 className="">
                    <NavLink to={`/datail/${id}`}>{theme}</NavLink>
                </h3>

                <div className="info">
                    <span><i className=""></i> {option} </span>
                    <span><i className=""></i> NIVEAU {niveau} </span>
                    <span><i className="date"></i> : {date}</span>
                </div>

                <p className="description">
                    {description}
                </p>

                <p className='read_more'><NavLink to={`/datail/${id}`}>Read More</NavLink></p>
            </div>
        </div>
    )
}

export default Card
