import React from 'react'
import AsideBar from '../Components/AsideBar'
import SideMenu from '../Components/SideMenu'
import Card from '../Components/Cart'
import { ListCards } from '../helpers/ListCard'


export interface BookDatas {
  id: string,
  image: string,
  theme: string,
  option: string,
  niveau: number,
  date: string,
  description?: string
}

const Home: React.FC = () => {
  const bookDatas: Array<BookDatas> = ListCards

  return (
    <div className='home_page layout'>
      <SideMenu />
      <main>
        <div className="cards">
          {bookDatas.map((book: BookDatas, key: number) =>
            <Card key={key} count={key} book={book} />
          )}
        </div>
      </main>
      <AsideBar />
    </div>
  )
}
export default Home